	import ePicker from '@/components/e-picker/e-picker.vue';
	import myPickerPart from "@/components/myPickerPartProps.vue";
	import uniIcon from "@/components/uni-icons/uni-icons.vue";
	let _this;
	function getDate(type) {
		const date = new Date();
	    date.setTime(date.getTime()+24*60*60*1000);
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
	    let hour = date.getHours();
		let minute = date.getMinutes()
		let second = date.getSeconds()
		
		if (type === 'start') {
			year = year - 60;
		} else if (type === 'end') {
			year = year + 2;
		}
		month = month > 9 ? month : '0' + month;;
		day = day > 9 ? day : '0' + day;
		hour = hour > 9 ? hour : '0' + hour;
		minute = minute > 9 ? minute : '0' + minute;
		second = second > 9 ? second : '0' + second;
	
		return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
	}
	export default {
		components:{
			myPickerPart,
			uniIcon,
			ePicker
		},
		data() {
			return {
				purcharseArr:[{id:'ss',label:'客户情绪'},{id:'sl',label:'产品需求'},{id:'vb',label:'市场行情'}],
				selectList:{
					warningLevelList:[],
					recordStatusList:[],
					failCauseList:[]
				},
				tradeName:'',      //关联的品名
				companyName:'',    //公司名
				form:{
					buyOrSellCode: '',      //联系人编码
					positionCode:'',		//资金状况编码
					channelCode:'',			//采购渠道编码
					creditCode:'',			//经营信用编码
					identityCode:'',		//采购身份编码
					strategyCode:'',		//经营策略编码
					sensitivityCode:'',	    //价格敏感度编码
					praiseCode:'',		    //市场口碑编码
					acceptanceCode:'',	    //品质接受度编码
					remarks:'',				//备注
					nextTime:'',		    //下次跟进时间
					nextRemarks:'',			//下次跟进备注
					rivalList:	[]		    //竞争对手（数组）
				},
				date: getDate({
					format: true
				})
				
			};
		},
		onLoad:function(options){
			_this = this
			this.form.buyOrSellCode = options.buyOrSellCode
			
			
		},
		methods:{
			
			handleClick:function(e){
				
				let reg = new RegExp(e.label)
				
				if (!reg.test(this.form.remarks)){
					
					this.purcharseArr.forEach((item)=>{
						if (item.id == e.id){
							this.$set(item,'isChecked',!item.isChecked)
						}
					})
					if (e.isChecked){
						if (this.form.remarks.length > 0){
							this.form.remarks = this.form.remarks + `\n${e.label}：`
						}else{
							this.form.remarks = this.form.remarks + `${e.label}：`
						}
					}
					
				}
	
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			confirm(obj){
				console.log(obj)
				this.date = obj.selectTime
			},
			
			areaInput:function(e){
				console.log(e)
			},
			
			async submit(){
				
				this.form.nextTime = this.date
				let data=this.form
                console.log(data)
				
				const res = await this.$http.post('/latent/add',{data:data})
				console.log(res)
				
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				prevPage.setData({
				   isDoRefresh:true
				})
				
				uni.redirectTo({
					url: `./detailFollowRecord?recordCode=${res.data.data.msg}`
				})
				
			}
		},
		
	}
